<template>
    <view class="order_freight">
        <!-- tabbar -->
        <cu-custom><block slot="content">抢货单</block></cu-custom>
        <view class="order_freight_list">
            <!-- 导航栏 -->
            <scroll-view scroll-x class="bg-white nav">
                <view class="flex text-center navBox">
                    <view class="cu-item flex-sub" :class="item.id==tabCur?'things_active_color':''" v-for="(item,index) in tabData" :key="index" @tap="tabSelect" :data-id="item.id">
                        {{item.name}}
                    </view>
                </view>
            </scroll-view>
            <!-- 待确认-列表 -->
            <scroll-view v-if="tabCur == 1" class="freight_list" scroll-y="true">
                <view class="freight_item" v-for="(item,index) in listData[0].list" :key="index">
                    <!-- 货物信息 -->
                    <view class="freight_info" :data-id="item.id"  @tap="linkToOrderFreightInfo">
                        <view class="things_width_full flex justify-start align-center user_info">
                            <view class="imageBox">
                                <image class="user_cover" :src="item.path" mode="widthFix"/>
                            </view>
                            <view class="name">{{item.nickname}}</view>
                            <view class="flex justify-start align-center score">
                                <image class="score_icon" src="./../../../../../../static/images/global/score_icon.png" mode="widthFix" />
                                <view class="score_num">{{item.reputation}}</view>
                                <view class="identity_text">{{item.userType}}</view>
                            </view>
                        </view>
                        <view class="freight_single_info">货物名称：{{item.goodsName}}</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">重量：{{item.weight}}吨</view>
                            <view class="freight_right_info">运输总价格：￥{{item.totalMoney}}</view>
                        </view>
                        <view class="freight_single_info">货物类型：{{item.goodsType}}</view>
                        <view class="freight_single_info">装车地点：{{item.loadingAddress}}</view>
                        <view class="freight_single_info">卸车地点：{{item.unloadingAddress}}</view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-end align-center operating">
                        <button class="cu-btn bg-white cancel_btn" :disabled="buttonType" :data-index="index" :data-id="item.id" @tap.stop="cancelOrderFun">取消订单</button>
                    </view>
                </view>
                <view v-if="listData[0].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 待付款-列表 -->
            <scroll-view v-if="tabCur == 2" class="freight_list" scroll-y="true">
                <view class="freight_item" v-for="(item,index) in listData[1].list" :key="index">
                    <view class="freight_info" :data-id="item.id" @tap="linkToOrderFreightInfo">
                        <view class="things_width_full flex justify-start align-center user_info">
                            <view class="imageBox">
                                <image class="user_cover" :src="item.path" mode="widthFix"/>
                            </view>
                            <view class="name">{{item.nickname}}</view>
                            <view class="flex justify-start align-center score">
                                <image class="score_icon" src="./../../../../../../static/images/global/score_icon.png" mode="widthFix" />
                                <view class="score_num">{{item.reputation}}</view>
                                <view class="identity_text">{{item.userType}}</view>
                            </view>
                        </view>
                        <view class="freight_single_info">货物名称：{{item.goodsName}}</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">重量：{{item.weight}}吨</view>
                            <view class="freight_right_info">运输总价格：￥{{item.totalMoney}}</view>
                        </view>
                        <view class="freight_single_info">货物类型：{{item.goodsType}}</view>
                        <view class="freight_single_info">装车地点：{{item.loadingAddress}}</view>
                        <view class="freight_single_info">卸车地点：{{item.unloadingAddress}}</view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-end align-center operating">
                        <button class="cu-btn bg-white cancel_btn" :disabled="buttonType" :data-index="index" :data-id="item.id" @tap.stop="cancelOrderFun">取消订单</button>
                    </view>
                </view>
                <view v-if="listData[1].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 审核中-列表 -->
            <scroll-view v-if="tabCur == 3" class="freight_list" scroll-y="true">
                <view class="freight_item" v-for="(item,index) in listData[2].list" :key="index">
                    <view class="freight_info" :data-id="item.id" @tap="linkToOrderFreightInfo">
                        <view class="things_width_full flex justify-start align-center user_info">
                            <view class="imageBox">
                                <image class="user_cover" :src="item.path" mode="widthFix"/>
                            </view>
                            <view class="name">{{item.nickname}}</view>
                            <view class="flex justify-start align-center score">
                                <image class="score_icon" src="./../../../../../../static/images/global/score_icon.png" mode="widthFix" />
                                <view class="score_num">{{item.reputation}}</view>
                                <view class="identity_text">{{item.userType}}</view>
                            </view>
                        </view>
                        <view class="freight_single_info">货物名称：{{item.goodsName}}</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">重量：{{item.weight}}吨</view>
                            <view class="freight_right_info">运输总价格：￥{{item.totalMoney}}</view>
                        </view>
                        <view class="freight_single_info">货物类型：{{item.goodsType}}</view>
                        <view class="freight_single_info">装车地点：{{item.loadingAddress}}</view>
                        <view class="freight_single_info">卸车地点：{{item.unloadingAddress}}</view>
                    </view>
                </view>
                <view v-if="listData[2].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 运输中-列表 -->
            <scroll-view v-if="tabCur == 4" class="freight_list" scroll-y="true">
                <view class="freight_item" v-for="(item,index) in listData[3].list" :key="index">
                    <view class="freight_info" :data-id="item.id" @tap="linkToOrderFreightInfo">
                        <view class="things_width_full flex justify-start align-center user_info">
                            <view class="imageBox">
                                <image class="user_cover" :src="item.path" mode="widthFix"/>
                            </view>
                            <view class="name">{{item.nickname}}</view>
                            <view class="flex justify-start align-center score">
                                <image class="score_icon" src="./../../../../../../static/images/global/score_icon.png" mode="widthFix" />
                                <view class="score_num">{{item.reputation}}</view>
                                <view class="identity_text">{{item.userType}}</view>
                            </view>
                        </view>
                        <view class="freight_single_info">货物名称：{{item.goodsName}}</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">重量：{{item.weight}}吨</view>
                            <view class="freight_right_info">运输总价格：￥{{item.totalMoney}}</view>
                        </view>
                        <view class="freight_single_info">货物类型：{{item.goodsType}}</view>
                        <view class="freight_single_info">装车地点：{{item.loadingAddress}}</view>
                        <view class="freight_single_info">卸车地点：{{item.unloadingAddress}}</view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-end align-center operating" v-if="item.status === 8">
                        <button class="cu-btn bg-white confirm_btn" :disabled="buttonType" :data-id="item.id" @tap="linkToUploadReceipt">完成运输</button>
                    </view>
                </view>
                <view v-if="listData[3].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 已完成-列表 -->
            <scroll-view v-if="tabCur == 5" class="freight_list" scroll-y="true">
                <view class="freight_item" v-for="(item,index) in listData[4].list" :key="index">
                    <view class="freight_info" :data-id="item.id" @tap="linkToOrderFreightInfo">
                        <view class="things_width_full flex justify-start align-center user_info">
                            <view class="imageBox">
                                <image class="user_cover" :src="item.path" mode="widthFix"/>
                            </view>
                            <view class="name">{{item.nickname}}</view>
                            <view class="flex justify-start align-center score">
                                <image class="score_icon" src="./../../../../../../static/images/global/score_icon.png" mode="widthFix" />
                                <view class="score_num">{{item.reputation}}</view>
                                <view class="identity_text">{{item.userType}}</view>
                            </view>
                        </view>
                        <view class="freight_single_info">货物名称：{{item.goodsName}}</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">重量：{{item.weight}}吨</view>
                            <view class="freight_right_info">运输总价格：￥{{item.totalMoney}}</view>
                        </view>
                        <view class="freight_single_info">货物类型：{{item.goodsType}}</view>
                        <view class="freight_single_info">装车地点：{{item.loadingAddress}}</view>
                        <view class="freight_single_info">卸车地点：{{item.unloadingAddress}}</view>
                    </view>
                    <!-- 操作区域 -->
                    <!-- <view class="flex justify-end align-center operating">
                        <button class="cu-btn bg-white assess_btn">评价</button>
                    </view> -->
                </view>
                <view v-if="listData[4].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
import { grabsGoodsList_api,cancelOrder_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            tabCur: 1,
            scrollLeft: 0,
            tabData: [
                {
                    id: 1,
                    value: 10,
                    name: '待确认'
                },
                {
                    id: 2,
                    value: 20,
                    name: '待付款'
                },
                {
                    id: 3,
                    value: 30,
                    name: '审核中'
                },
                {
                    id: 4,
                    value: 40,
                    name: '运输中'
                },
                {
                    id: 5,
                    value: 50,
                    name: '已完成'
                }
            ],
            listData: [
                // 待确认
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 待支付
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 审核中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 运输中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 已完成
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                }
            ],
            // 全局按钮状态
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.base_refreshPage': function(val) {
            if (val) {
                // 更新数据
                that.clearPageFun();
                that.base({name:'refreshPage',value:false});
            }
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.listData = [
                // 待确认
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 待支付
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 审核中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 运输中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 已完成
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                }
            ];
            that.getFreightOrder();
        },
        /**导航选择
         * @description: 
         * @param {*} e
         * @return {*}
         */
        tabSelect(e) {
            if(that.tabCur == e.currentTarget.dataset.id) return false;
            that.tabCur = e.currentTarget.dataset.id;
            that.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
            // 判断当前列表有没有数据,没有就qui请求数据
            if (that.listData[that.tabCur - 1].list.length <= 0 && that.listData[that.tabCur - 1].pageMore) {
                that.getFreightOrder();
            }
        },
        /**取消订单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        cancelOrderFun(e) {
            that.buttonType = true;
            let params = {
                order_id: e.currentTarget.dataset.id,
                type: that.base_cancelOrderType[0].value,
            };
            cancelOrder_api(params,that).then(res=>{
                that.$delete(that.listData[that.tabCur-1].list, e.currentTarget.dataset.index);
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'取消成功'}});
                that.base({name:'refreshPage',value:true});
                that.buttonType = false;
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
            })
        },
        /** 跳转订单货物详情
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToOrderFreightInfo(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/order/freight/info/index?order_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**跳转上传收据
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToUploadReceipt(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/order/freight/uploadReceipt/index?order_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**获取抢货单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFreightOrder(e) {
            let params = {
                page: that.listData[that.tabCur-1].page,
                page_size: that.listData[that.tabCur-1].page_size,
                status: that.tabData[that.tabCur-1].value
            };
            grabsGoodsList_api(params,that).then(res=>{
                console.log(res);
                that.updateListData(res.list,res.total);
            }).catch(err=>{
                console.log(err);
            })
        },
        /**更新数据
         * @description: 
         * @param {*} data 数组列表
         * @param {*} total 列表总长度
         * @return {*}
         */
        updateListData(data=[],total=0) {
            console.log(data);
            switch (Number(that.tabData[that.tabCur-1].value)) {
                case 10:
                    that.listData[0].list = that.listData[0].list.concat(data);
                    if (that.listData[0].list.length >= total) {
                        that.listData[0].pageMore = false;
                    }else{
                        that.listData[0].page++;
                    }
                    break;
                case 20:
                    that.listData[1].list = that.listData[1].list.concat(data);
                    if (that.listData[1].list.length >= total) {
                        that.listData[1].pageMore = false;
                    }else{
                        that.listData[1].page++;
                    }
                    break;
                case 30:
                    that.listData[2].list = that.listData[2].list.concat(data);
                    if (that.listData[2].list.length >= total) {
                        that.listData[2].pageMore = false;
                    }else{
                        that.listData[2].page++;
                    }
                    break;
                case 40:
                    that.listData[3].list = that.listData[3].list.concat(data);
                    if (that.listData[3].list.length >= total) {
                        that.listData[3].pageMore = false;
                    }else{
                        that.listData[3].page++;
                    }
                    break;
                case 50:
                    that.listData[4].list = that.listData[4].list.concat(data);
                    if (that.listData[4].list.length >= total) {
                        that.listData[4].pageMore = false;
                    }else{
                        that.listData[4].page++;
                    }
                    break;
                default:
                    break;
            }
        },
        /**上拉加载
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getMoreData(e) {
            let getType = false;
            switch (Number(that.tabData.value)) {
                case 10:
                    getType = that.listData[0].pageMore;
                    break;
                case 20:
                    getType = that.listData[1].pageMore;
                    break;
                case 30:
                    getType = that.listData[2].pageMore;
                    break;
                case 40:
                    getType = that.listData[3].pageMore;
                    break;
                case 50:
                    getType = that.listData[4].pageMore;
                    break;
                default:
                    break;
            }
            if (!getType) return false;
            that.getFreightOrder();
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        that.tabCur = options.type?options.type:1;
        that.getFreightOrder();
    }
}
</script>

<style lang="scss" scoped>
    @import "./index";
</style>